*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  background: linear-gradient(135deg, #70b7ff, #4584f1);
  min-height: 100vh;
  color: #333;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 2rem 1rem;
}

/* Container for entire app */
.weather-container {
  width: 100%;
  max-width: 500px;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  margin-top: 2rem;
}

/* Form Styling */
form {
  padding: 1.5rem;
  border-bottom: 1px solid #eaeaea;
  background-color: white;
}

form label {
  font-size: 1.2rem;
  font-weight: 500;
  color: #333;
  margin-bottom: 0.5rem;
  display: block;
}

/* Properly style both the original input and new location-input */
input[type='text'],
#location-input {
  width: 100%;
  padding: 0.75rem 1rem;
  border-radius: 8px;
  border: 2px solid #e0e0e0;
  font-size: 1rem;
  margin-bottom: 1rem;
  transition: border-color 0.3s;
}

input[type='text']:focus,
#location-input:focus {
  outline: none;
  border-color: #4584f1;
  box-shadow: 0 0 0 3px rgba(69, 132, 241, 0.1);
}

.button-group {
  display: flex;
  gap: 10px;
  margin-top: 0.5rem;
}

button {
  cursor: pointer;
  border: none;
  border-radius: 8px;
  font-weight: 500;
  transition: all 0.2s ease;
}

#search {
  background-color: #4584f1;
  color: white;
  padding: 0.75rem 1.5rem;
  flex: 1;
}

#search:hover {
  background-color: #3571d8;
  transform: translateY(-2px);
}

#toggle {
  background-color: #f5f5f5;
  color: #666;
  padding: 0.75rem 1rem;
  border: 1px solid #e0e0e0;
}

#toggle:hover {
  background-color: #e9e9e9;
}

#fahrenheit,
#celsius {
  transition: color 0.3s ease;
}

#fahrenheit[style='color: black'],
#celsius[style='color: black'] {
  color: #4584f1 !important;
  font-weight: 600;
}

/* Error Message */
#error {
  color: #e53935;
  font-size: 0.875rem;
  margin-top: 0.5rem;
  display: block;
  padding-left: 0.25rem;
  position: static;
}

/* Loading State */
#loading {
  padding: 3rem 1.5rem;
  text-align: center;
}

#loading h1 {
  font-size: 1.5rem;
  color: #666;
  margin: 0;
  animation: pulse 1.5s infinite ease-in-out;
}

@keyframes pulse {
  0% {
    opacity: 0.6;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0.6;
  }
}

/* Weather Information Display */
#info {
  padding: 2rem 1.5rem;
  position: static;
  margin: 0;
}

#info > div:first-child {
  margin-bottom: 2rem;
  text-align: center;
}

#location {
  font-size: 1.8rem;
  color: #333;
  margin-bottom: 0.5rem;
  font-weight: 600;
}

#time {
  font-size: 1rem;
  color: #666;
  font-weight: 400;
}

/* Fixed alignment for temperature and condition */
#info > div:last-child {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  background-color: rgba(69, 132, 241, 0.1);
  border-radius: 12px;
  padding: 1.5rem;
  text-align: center;
}

#temperature,
#condition {
  font-size: 1.5rem;
  font-weight: 500;
  color: #333;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  padding-bottom: 1.5rem;
}

#temperature::after,
#condition::after {
  position: absolute;
  bottom: 0;
  font-size: 0.875rem;
  color: #666;
  font-weight: 400;
}

#temperature::after {
  content: 'Temperature';
}

#condition::after {
  content: 'Condition';
}

/* Weather Icons - add these later with proper imagery */
.weather-icon {
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
}

/* Responsive Adjustments */
@media (max-width: 600px) {
  .weather-container {
    border-radius: 12px;
  }

  #location {
    font-size: 1.5rem;
  }

  /* Keep the grid layout on mobile */
  #info > div:last-child {
    grid-template-columns: 1fr 1fr;
  }
}
